@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #090e1d;
  color: #ecf1ff; }

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #F5F5F5;
  border-radius: 2px; }

*::-webkit-scrollbar-thumb {
  background-color: #000000;
  border-radius: 2px; }

.text-dark {
  color: #090e1d !important; }

.text-light {
  color: #ecf1ff !important; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

h1 {
  font-weight: 900; }

h2 {
  font-weight: 900; }

h3 {
  font-weight: 900; }

h4 {
  font-weight: 900; }

h5 {
  font-weight: 900; }

.icon {
  -webkit-filter: invert(0.65) grayscale(0.5);
          filter: invert(0.65) grayscale(0.5); }

.btn-link:hover {
  text-decoration: none; }

.dropdown-toggle {
  min-width: 150px; }

.App {
  padding-top: 5rem;
  width: 100%;
  overflow-y: auto; }

.navbar {
  background-color: #090e1d; }
  .navbar .navbar-brand {
    color: #ecf1ff; }
  .navbar .nav-item {
    border-radius: 1.25rem;
    font-weight: 600;
    font-size: 80%;
    color: #ecf1ff; }
  .navbar .nav-item:hover {
    background-color: #04060d; }

.movie-list .dropdown {
  max-width: 50%;
  flex-basis: 50%; }
  .movie-list .dropdown .dropdown-menu::after {
    content: ''; }
  .movie-list .dropdown .dropdown-toggle {
    width: 100%; }

.movie-list .card {
  max-width: 400px;
  background: no-repeat center /cover;
  margin: 1rem auto;
  margin-bottom: 1rem;
  height: 300px;
  position: relative;
  cursor: pointer;
  border-radius: 1rem;
  border: none; }

.movie-list .card::after {
  content: "";
  position: absolute;
  left: -1px;
  top: -1px;
  width: 105%;
  height: 105%;
  background-color: rgba(9, 14, 29, 0.5);
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s; }

.movie-list .card:hover::after {
  background-color: rgba(9, 14, 29, 0.7);
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s; }

.movie-list .card-body {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  text-align: left; }
  .movie-list .card-body .btn-link {
    position: absolute;
    right: 0;
    top: 0; }
  .movie-list .card-body h4, .movie-list .card-body p {
    margin: 0;
    text-shadow: 0 1px 2px #090e1d; }
  .movie-list .card-body > * {
    text-shadow: 0 1px 1px #090e1d;
    margin: 0; }
  .movie-list .card-body .rating-genre {
    font-size: 0.75rem;
    display: flex;
    align-items: center; }

.movie-list .dropdown {
  margin-right: 1rem; }

.movie-list .dropdown:last-child {
  margin-right: 0; }

.movie-details {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1100;
  background: #090e1d no-repeat center fixed/cover;
  overflow-y: auto;
  -webkit-transition: all 0s linear;
  transition: all 0s linear; }
  .movie-details .backBtn {
    left: 1rem;
    top: 1rem;
    font-size: 2rem;
    color: #ecf1ff; }
  .movie-details .container-fluid {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    background-color: rgba(9, 14, 29, 0.75); }
  .movie-details .container-fluid::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
    /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 65%);
    /* Chrome10-25,Safari5.1-6 */
    background: -webkit-radial-gradient(center, ellipse, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 65%);
    background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 65%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=1 ); }
  .movie-details .container {
    min-height: 100vh;
    display: flex;
    align-items: center; }
    .movie-details .container .poster {
      border-radius: 1rem; }
    .movie-details .container .rating {
      font-size: 1.5rem;
      font-weight: 400;
      margin-left: 1rem; }
    .movie-details .container h1 {
      font-size: 3rem; }
    .movie-details .container h1, .movie-details .container h4, .movie-details .container p {
      margin: 0;
      text-shadow: 0 1px 2px #090e1d; }

.movie-details:after {
  content: "Movie DB";
  position: fixed;
  top: 50vh;
  left: 0;
  font-size: 10rem;
  font-weight: 900;
  color: rgba(236, 241, 255, 0.15);
  z-index: -1;
  width: 100%;
  text-align: center;
  line-height: 50vh; }

@media screen and (max-width: 420px) {
  .nav-item svg {
    -webkit-transform: scale(1.5);
            transform: scale(1.5); }
  .nav-item span:not(.badge) {
    display: none; } }

.fadeUp {
  -webkit-animation: fadeUp 0.5s ease 1 forwards;
          animation: fadeUp 0.5s ease 1 forwards; }

.fadeDown {
  -webkit-animation: fadeDown 0.5s ease 1 forwards;
          animation: fadeDown 0.5s ease 1 forwards; }

@-webkit-keyframes fadeUp {
  0% {
    -webkit-transform: scale(1.25) translateY(100px);
            transform: scale(1.25) translateY(100px);
    opacity: 0; }
  100% {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
    opacity: 1; } }

@keyframes fadeUp {
  0% {
    -webkit-transform: scale(1.25) translateY(100px);
            transform: scale(1.25) translateY(100px);
    opacity: 0; }
  100% {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
    opacity: 1; } }

@-webkit-keyframes fadeDown {
  0% {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.25) translateY(100px);
            transform: scale(1.25) translateY(100px);
    opacity: 0; } }

@keyframes fadeDown {
  0% {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.25) translateY(100px);
            transform: scale(1.25) translateY(100px);
    opacity: 0; } }

.searchPage .container {
  max-width: 500px; }
  .searchPage .container .btn-light {
    background-color: #fff; }
  .searchPage .container .input-group-lg > .form-control,
  .searchPage .container .input-group-lg > .input-group-append > .btn {
    border-radius: 1rem; }
  .searchPage .container .input-group > .form-control:not(:last-child) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  .searchPage .container .input-group-lg > .input-group-append > .btn:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .searchPage .container .list-group-item:first-child {
    border-radius: 0;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem; }
  .searchPage .container .list-group-item:last-child {
    border-radius: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem; }
  .searchPage .container .list-group-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer; }
    .searchPage .container .list-group-item .details {
      margin-left: 1rem; }
    .searchPage .container .list-group-item .details > span {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: row; }
    .searchPage .container .list-group-item img {
      max-width: 80px; }

